<template>
    <div id="footer">
      <div class="info">
        <p>Made by Richard Kajaste</p>
        <p>Copyright @ {{ date }}</p>
      </div>
    </div>
</template>

<script>
export default {
  name: 'FooterWrapper',
  data () {
    return {
      date: new Date().getFullYear()
    }
  }
}
</script>

<style>
#footer{
    font-size: 0.8rem;
    clear: both;
    position: relative;
    padding: 4em;
    background-color: black;
    color: darkgray;
    border: solid 1px black;
    max-width: 100vw;
}
.info{
  position: absolute;
  top: 5%;
  right: 5%;
}
</style>



