<template>
  <div id="app">
    <HeaderWrapper/>
    <router-view class="main-site"/>
    <FooterWrapper/>
  </div>
</template>

<script>
import HeaderWrapper from '@/components/HeaderWrapper'
import FooterWrapper from '@/components/FooterWrapper'
export default {
  name: 'App',
  components: {
    HeaderWrapper, FooterWrapper
  }
}
</script>

<style>
body {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
}
.main-site{
  min-height: 100vh;
  max-width: 100vw;
  background-image: url(./assets/bg.png);
  background-size: cover;
  background-repeat: no-repeat;
}
.main-site .description-container{
  position: absolute;
  text-align: center;
  padding: 2em 2em;
  width: 40%;
  top: 30%;
  left: 30%;
  background-color: rgba(0, 0, 0, 0.8);
  color: rgb(216, 90, 31);
  font-size: 1.2em;
  font-weight: bold;
}
</style>



